import React from 'react'
import { ContactForm1 } from './ContactForm1'
import { ContactForm2 } from './ContactForm2'
import { SubmitButton } from './SubmitButton'
import emailjs from 'emailjs-com';
export const Contact = () => {
  function sendEmail(e){
     e.preventDefault();
    emailjs.sendForm('service_f2yh46p', 'template_f3ww69i', e.target, 'user_vLjprGbM1FDruwIK0mHjm')
    .then((result) => { 
        console.log(result.text);
    }, (error) => {
        console.log(error.text);
    });e.target.reset();
    window.alert("Email Sent Successfully Thank you! :3")
}

  return (
    <form className="contact" id="contact" onSubmit={sendEmail}>
        <h1>CONTACT</h1>
        <ContactForm1 />
        <ContactForm2 />
        <SubmitButton />
    </form>
  )
}
